.btn {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  color: inherit;
  font: inherit;
  line-height: normal;
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  -webkit-appearance: none;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.5s ease;
  padding: 12px 16px;
  font-size: inherit;
  line-height: 16px;
  text-decoration: none;
}
.btn::-moz-focus-inner {
  border: 0;
  padding: 0;
}
.btn:focus {
  outline: none;
}
.btn em {
  font-weight: 700;
  font-style: normal;
}
.btn:hover {
  transition: background-color 0.5s ease;
}

.btn--style-a {
  background: #fa887a;
  color: #ffffff;
}
.btn--style-a:hover {
  background: #f85c49;
}

.btn--style-b {
  background: #2db88e;
  color: #ffffff;
}
.btn--style-b:hover {
  background: #238f6e;
}

.btn--style-c {
  color: #222222;
  opacity: 0.9;
  text-decoration: underline;
}
.btn--style-c:hover {
  opacity: 1;
}

.btn--style-d {
  background: #e9676c;
  color: #303e48;
}
.btn--style-d:hover {
  background: #e33a41;
}

.btn--style-e {
  background: #fff9f9;
  color: #303e48;
}
.btn--style-e:hover {
  background: #ffc6c6;
}

.btn--style-f {
  background: #ffd23b;
  color: #303e48;
}
.btn--style-f:hover {
  background: #ffc608;
}

.btn--style-g {
  background: #2db88e;
  color: #ffffff;
}
.btn--style-g:hover {
  background: #238f6e;
}

.btn--paypal {
  background: #009cde;
  text-decoration: none;
  color: white;
  display: none;
}
.btn--paypal:hover {
  background: #0078ab;
}
.btn--paypal em {
  background: url("../img/pp-white.svg") left center no-repeat;
  background-size: contain;
  padding-left: 24px;
